@import '../../variables';

$base03: #002b36;
$base02: #073642;
$base01: #f1eff7;
$base00: #f1eff7;
$base0: #839496;
$base1: #6d6db5;
$base2: #333454;
$base3: #292a44;
$yellow: #eedb68;
$orange: #e378fe;
$red: #e378fe;
$magenta: #ffa8fe;
$violet: #6c71c4;
$blue: #66d9ef;
$cyan: #94f2e7;
$green: #2ddca5;

code[class*='language-'],
pre[class*='language-'] {
  color: $base00;
  font-size: 1em;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;

  line-height: 1.5;

  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;

  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}

pre[class*='language-']::-moz-selection,
pre[class*='language-'] ::-moz-selection,
code[class*='language-']::-moz-selection,
code[class*='language-'] ::-moz-selection {
  background: $base02; /* base02 */
}

pre[class*='language-']::selection,
pre[class*='language-'] ::selection,
code[class*='language-']::selection,
code[class*='language-'] ::selection {
  background: $base02; /* base02 */
}

/* Code blocks */
pre[class*='language-'] {
  padding: 1em;
  margin: 0.5em 0;
  overflow: auto;
  border-radius: 0.3em;
}

:not(pre) > code[class*='language-'],
pre[class*='language-'] {
  background-color: $base3; /* base3 */
}

/* Inline code */
:not(pre) > code[class*='language-'] {
  padding: 0.1em;
  border-radius: 0.3em;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: $base1; /* base1 */
}

.token.punctuation {
  color: $base01; /* base01 */
}

.namespace {
  opacity: 0.7;
}

.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
  color: $blue; /* blue */
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.url,
.token.inserted {
  color: $cyan; /* cyan */
}

.token.entity {
  color: $base00; /* base00 */
  background: $base2; /* base2 */
}

.token.atrule,
.token.attr-value,
.token.keyword {
  color: $yellow;
}

.token.function,
.token.class-name {
  color: $green;
}

.token.regex,
.token.important,
.token.variable {
  color: $orange; /* orange */
}

.token.important,
.token.bold {
  font-weight: bold;
}
.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}
